@import '../../../stylesheet/vars.scss';

.TransactionAddForm__closeBtn {
  border: none;
  position: absolute;
  right: 20px;
  top: 20px;
  background-color: #fff;
  display: flex;
  cursor: pointer;
}

.TransactionAddForm__title {
  font-family: $poppins;
  margin-bottom: 40px;
  margin-top: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 45px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  color: #000;

  @media screen and (min-width: 768px) {
    font-size: 30px;
    line-height: 26px;
  }
}

.TransactionAddForm__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

//Checkbox
.checkbox {
  &__wrapper {
    margin-bottom: 40px;
    display: flex;
    align-items: center;
  }

  &__span {
    font-weight: 700;
    font-style: 16px;
    line-height: 24px;
    color: #e0e0e0;
  }

  &__button {
    margin: 0 20px;
    margin-left: 15px;
    margin-right: 15px;
  }
}

.active-income {
  color: #24cca7;
}

.active-spend {
  color: #ff6596;
}

.switch-icon__spend {
  width: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.add-transaction__icon {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.react-switch-bg {
  border: 1px solid #e0e0e0;
}

