.TransactionAddForm__input {
  border: none;
  border-bottom: 1px solid #e0e0e0;
  margin-bottom: 40px;
}
.TransactionAddForm__addBtn {
  width: 300px;
  height: 50px;
  margin-bottom: 20px;
  // font-family: Circe;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  border: none;
  background: #24cca7;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;

  color: #ffffff;
}

.TransactionAddForm__cancelBtn {
  width: 300px;
  height: 50px;
  border: 1px solid #4a56e2;
  background: #fff;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 40px;
  // font-family: Circe;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: #4a56e2;
}
